import { loginAPI, logoutAPI } from '../util/api';

export const login = (token) => {
    return {
        type: 'LOGIN',
        token: token,
    };
};

export const loginFail = (error) => {
    return {
        type: 'LOGIN_FAIL',
        error: error,
    };
};

export const logout = () => {
    return {
        type: 'LOGOUT',
    };
};

export const logoutFail = (error) => {
    return {
        type: 'LOGOUT_FAIL',
        error: error,
    };
};

export const loginTry = (loginInfo, token) => async (dispatch) => {
    try {
        const res = await loginAPI(loginInfo);
        dispatch(login(token));
    } catch (error) {
        dispatch(loginFail(error));
    }
};

export const logoutTry = () => async (dispatch) => {
    try {
        const res = await logoutAPI();
        dispatch(logout());
    } catch (error) {
        console.log(error);
        dispatch(logoutFail(error));
    }
};
