const authReducer = (state = { status: false, token: null, error: null }, action) => {
    switch (action.type) {
        case 'LOGIN': {
            return { status: true, token: action.token, error: null };
        }

        case 'LOGOUT': {
            return { status: false, token: null, error: null };
        }

        case 'LOGIN_FAIL': {
            return { status: false, token: null, error: action.error };
        }

        case 'LOGOUT_FAIL': {
            return { status: state.status, token: state.token, error: action.error };
        }

        default:
            return state;
    }
};

export default authReducer;
