import React, { Component } from 'react';
import { connect } from 'react-redux';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { withCookies } from 'react-cookie';
import Nav from './Components/Nav';
import Main from './Components/Main';
import About from './Components/About';
import Books from './Components/Store/Books';
import GatheringMain from './Components/Gathering';
import Login from './Components/Login';
import MyPage from './Components/MyPage';
import Footer from './Components/Footer';

class App extends Component {
    render() {
        return (
            <div className="App">
                <Router>
                    <Route path="/" render={() => <Nav cookies={this.props.cookies} />} />
                    <div className="lg:grid lg:grid-cols-12 lg:gap-2">
                        <div
                            className="lg:mt-3 lg:mb-3 
                                        sm:mx-auto sm:px-10 sm:mt-8
                                        sm:flex sm:w-full 
                                        lg:col-start-3 lg:col-end-11"
                        >
                            <Switch>
                                <Route
                                    exact
                                    path="/"
                                    render={() => <Main cookies={this.props.cookies} />}
                                />
                                <Route exact path="/main" to="/" />
                                <Route
                                    exact
                                    path="/about"
                                    render={() => <About cookies={this.props.cookies} />}
                                />
                                <Route
                                    exact
                                    path="/gathering"
                                    render={() => <GatheringMain cookies={this.props.cookies} />}
                                />
                                <Route
                                    exact
                                    path="/package"
                                    render={() => <Books cookies={this.props.cookies} />}
                                />
                                <Route
                                    exact
                                    path="/store"
                                    render={() => <Books cookies={this.props.cookies} />}
                                />
                                <Route
                                    exact
                                    path="/notice"
                                    render={() => <Books cookies={this.props.cookies} />}
                                />

                                <Route
                                    exact
                                    path="/mypage"
                                    render={() => <MyPage cookies={this.props.cookies} />}
                                />

                                <Route
                                    exact
                                    path="/login"
                                    render={() => <Login cookies={this.props.cookies} />}
                                />

                                <Route
                                    exact
                                    path="/register"
                                    render={() => <Login cookies={this.props.cookies} />}
                                />
                            </Switch>
                        </div>
                    </div>
                </Router>
                <Footer />
            </div>
        );
    }
}
const MapStateToProps = (state) => ({
    logged: state.logged,
});

const MapDispatchToProps = {};

export default connect(MapStateToProps, MapDispatchToProps)(withCookies(App));
